.person-card {
    border-radius: 20px 20px 0 0;
    margin: 0 20px;
    margin-bottom: 50px;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    max-width: 300px;
    display: flex;
    flex-direction: column;
}

.person-card img {
    border-radius: 20px 20px 0 0;
    height: 300px;
    object-fit: cover;
}

.person-card article {
    margin-top: -20px;
    padding: 10px 20px;
    background-color: var(--darkgreen);
    border-radius: 20px 20px 0 0;
    text-align: center;
}

.person-card article h3 {
    font-size: 20px;
    color: var(--darkblue);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.person-card article h4 {
    font-size: 16px;
    color: var(--white);
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

